<template>
	<view class="container">
		<view class="quikmenu" v-if="lists.length>0">
			<view class="item" v-for="(item,index) in lists" :key="index">
				<view class="info">
					<view class="title"  @click="selectAddress(index)">
						<text class="text">{{item.realname}}</text>
						<text class="text">{{item.mobile}}</text>						 
					</view>
					<view class="address" ><uni-icons type="location-filled" size="18" color="#999999"></uni-icons> {{item.address_details}}</view>
					<view class="btns">
						<view v-if="item.is_default==1" class="btn_default">默认地址</view>
						<navigator class="btn" style="margin-left: auto;" hover-class="none" :url="'./edit?id='+item.id">编辑</navigator>
						<view class="btn active" @click="showModal(item)">删除</view>
					</view>
				</view> 
			</view>
			<navigator class="add" hover-class="none" url="./add" style="margin-top:60rpx;">
				新增地址
			</navigator>
		</view>
		
		<view class="empty" v-else>
			<image src="../../static/images/af02ec2419a2ef2b34e6628310739efb48d008f21924c-cqD41q@2x.png" mode="scaleToFill"></image>
			<view class="empty-title">暂无数据</view>
			<navigator class="add" hover-class="none" url="./add">
				新增地址
			</navigator>
		</view>		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				showPopup: false,
				lists: []
			}
		},
		onShow() {
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
			this.getInfo()

		},
		methods: {
			selectAddress(index){
				/*选择订单地址*/
				uni.$emit('selectAddress',{address:this.lists[index]})
				uni.navigateBack({
					delta:1
				})
			},
			async getInfo() {
				let res = await this.$u.get('/v2/member/address/index', {
					uid: this.vuex_user.id
				})
				console.log(res)
				this.lists = res.data.data
			},
			showModal(item) {
				uni.showModal({
					title: '提示',
					content: '确定要删除这个地址吗',
					success: (res)=>{
						if (res.confirm) {
							this.del(item.id)
						} else if (res.cancel) {
							return
						}
					}
				});
			},
			async del(id) {
				let res = await this.$u.get('/v2/member/address/del', {
					id: id
				})
				if (res.code == 0) {
					this.$u.toast(res.message)
					return
				}
				uni.navigateTo({
					url: './index'
				})
			},
		}
	}
</script>
<style>
	page{
		background-color: #F5F5F5;
	}
</style>
<style scoped lang="scss">
	.container {
		min-height: 100vh;
		/*  #ifdef  H5 */
		min-height: calc(100vh - var(--window-top));
		/*  #endif  */
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.empty{
		display: flex;
		flex-direction: column;
		align-items: center;
		height: 100vh;
		width: 100vw;
		background-color: #fff;
	}
	.empty image{
		width: 300rpx;
		height: 300rpx;
		margin-bottom: 20rpx;
	}
	.empty .empty-title{
		font-size: 26rpx;
		color: #999999;
		margin-bottom: 30rpx;
	}
	.quikmenu {
		display: flex;
		flex-direction: column;
	}

	.quikmenu .item {
		background-color: #fff;
		margin: 20rpx 20rpx 0 20rpx;
		border-radius: 16rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx;
	}
	.quikmenu .item .info{
		flex: 1;
	}
	.quikmenu .item .info .title {
		margin-bottom: 15rpx;
		font-size: 34rpx;
		font-weight: 500;
	}
	.quikmenu .item .info .title .text{
		padding-right: 10rpx;
	}
	.quikmenu .item .info .address {
		font-size: 28rpx;
		color: #999;
	}
	.quikmenu .item .info .btns{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 20rpx;
		border-top: #eee 1px solid;
		padding-top: 20rpx;
	}
	.quikmenu .item .info .btns .btn_default{
		width: 140rpx;
		height: 48rpx;
		background-color: #eee;
		font-size: 24rpx;
		font-weight: 500;
		color: #666666;
		border-radius: 24rpx;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.quikmenu .item .info .btns .btn{
		width: 140rpx;
		height: 60rpx;
		border-radius: 30rpx;
		border: #B1B1B1 1px solid;
		font-size: 28rpx;
		font-weight: 500;
		color: #666666;
		display: flex;
		align-items: center;
		justify-content: center;
		box-sizing: border-box;
		margin-left: 20rpx;
	}
	.quikmenu .item .info .btns .btn.active{
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		color: #FFFFFF;
		border:none;
	}
	.add{
		margin: 20rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		height: 88rpx;
		border-radius: 44rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		width: 690rpx;
	}
</style>
